document.addEventListener('DOMContentLoaded', () => {
  const brandSelect = document.querySelector('select[name="brand"]');
  const categorySelect = document.querySelector('select[name="category"]');
  const form = document.querySelector('#filters-form');

  async function loadFilters() {
    try {
      const response = await fetch('/wp-json/catalog/v1/cars');
      if (!response.ok) throw new Error('Помилка HTTP: ' + response.status);
      const data = await response.json();

      clearOptionsExceptFirst(brandSelect);
      clearOptionsExceptFirst(categorySelect);

      data.brands?.forEach(brand => {
        brandSelect.appendChild(createOption(brand.value, brand.label));
      });

      data.categories?.forEach(category => {
        categorySelect.appendChild(createOption(category.value, category.label));
      });
    } catch (err) {
      console.error('Помилка при завантаженні фільтрів:', err);
    }
  }

  function createOption(value, label) {
    const option = document.createElement('option');
    option.value = value;
    option.textContent = label;
    return option;
  }

  function clearOptionsExceptFirst(select) {
    while (select.options.length > 1) {
      select.remove(1);
    }
  }

  if (form) {
    form.addEventListener('submit', function (e) {
      e.preventDefault();

      const selectedBrand = brandSelect.value;
      const selectedCategory = categorySelect.value;

      console.log('Обраний бренд:', selectedBrand);
      console.log('Обрана категорія:', selectedCategory);

    });
  }

  loadFilters();
});
